@import "https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.main__container {
  height: 100vh;
  background-image: url("Paths-Hero-Image.170eeabe.png");
  background-position: center;
  background-size: cover;
  position: relative;
}

.main__navbar {
  z-index: 10;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 5em 10em;
  transition: -webkit-backdrop-filter .5s, backdrop-filter .5s, background-color .5s, padding .2s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.main__hamburger {
  z-index: 100;
  transition: transform .2s;
  display: none;
}

.bar {
  width: 25px;
  height: 3px;
  transition: all .2 ease;
  background-color: #eee;
  margin: 5px auto;
  display: block;
}

.main__hamburger:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.main__hamburger:focus {
  outline: none;
}

.navbar-background {
  -webkit-backdrop-filter: blur(2rem);
  backdrop-filter: blur(2rem);
  background-color: #030716d0;
  padding: 2em 10em;
}

.main__navbar img {
  height: 16px;
}

.main__navbar ul {
  gap: 5em;
  display: flex;
}

.main__navbar ul li {
  list-style: none;
}

.main__navbar ul li a {
  color: #eee;
  text-decoration: none;
  transition: all .2s;
}

.current-page {
  color: #0000;
  background-image: linear-gradient(to right, #409dea, #7fe3f9);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
  font-weight: 700;
}

.main__navbar ul li a:hover {
  color: #0000;
  background-image: linear-gradient(to right, #409dea, #7fe3f9);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
}

.main__body {
  padding: 42vh 25em;
}

.main__body h1, .main__body p {
  color: #eee;
}

.main__body-header {
  margin-bottom: .2em;
  font-size: 4em;
}

.main__body p span {
  color: #0000;
  background-image: linear-gradient(to right, #409dea, #7fe3f9);
  background-size: cover;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
  font-weight: 700;
}

.main__social-icons {
  flex-direction: column;
  align-items: center;
  gap: 1.5em;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 12em;
}

.main__social-icons img {
  width: 1.2em;
}

.main__social-line {
  width: 1px;
  height: 150px;
  background-color: #ffffff79;
}

.projects-section {
  background-color: #030716;
  padding-top: 7.5em;
}

.projects-section h1 {
  color: #eee;
  margin: 0 5em;
  font-size: 3em;
}

.projects-section p {
  color: #aeb7be;
  line-height: 1.5em;
}

.projects-section .project-description h3 {
  color: #0000;
  background-image: linear-gradient(to right, #298cdd, #a2eeff);
  background-size: cover;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
  font-size: 2em;
  font-weight: 700;
}

.projects-section .project-subtitle {
  color: #445069;
}

.project {
  gap: 3em;
  padding: 5em 15em;
  display: flex;
}

.project-description, .project-thumbnail {
  width: 50%;
}

.project-thumbnail {
  z-index: 0;
  position: relative;
  overflow: hidden;
}

.project-description {
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.project-thumbnail img {
  max-width: 100%;
  transition: transform .4s ease-in-out;
}

.project-thumbnail img:hover {
  transform: scale(1.1);
}

.project-details {
  flex-direction: column;
  gap: 2em;
  display: flex;
}

.project-link {
  align-items: center;
  gap: .5em;
  display: flex;
}

.project-link img {
  height: .8em;
}

.project-link a {
  color: #eee;
  text-decoration: none;
}

.project-alternate {
  background-color: #0a0e1e;
}

.skills__container {
  background-image: url("Section-Background.5567bf85.png");
  background-position: center;
  background-size: cover;
  flex-direction: column;
  padding: 7.5em 15em;
  display: flex;
}

.skills__container h1, .skills__container p {
  color: #eee;
  margin: 0;
}

.skills__container p {
  color: #aeb7be;
  line-height: 1.5em;
}

.skills__container h1 {
  margin-bottom: .6em;
  font-size: 3em;
}

.skills__container h3 {
  color: #0000;
  background-image: linear-gradient(to right, #089bdf, #caf5ff);
  background-size: cover;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
  margin-bottom: 1em;
}

.skills__design-container, .skills__developmenr-container {
  padding: 0 0;
}

.skills__design, .skills__development {
  align-items: flex-start;
  gap: 6em;
  display: flex;
}

.skills__design {
  margin-bottom: 2em;
}

.skills__design-right, .skills__design-left, .skills__development-right, .skills__development-left {
  width: 50%;
}

.skills__design-right, .skills__development-right {
  flex-wrap: wrap;
  gap: 3em;
  display: flex;
}

.skills__design-right img, .skills__development-right img {
  height: 2.8em;
  transition: transform .2s;
}

.skills__design-right img:hover, .skills__development-right img:hover {
  transform: scale(1.2);
}

.contact__container {
  height: 100vh;
  background-color: #030716;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1.5em;
  display: flex;
}

.contact__container h1 {
  color: #eee;
  margin: 0;
  font-size: 3em;
}

.contact__icons-container {
  gap: 1em;
  display: flex;
}

.contact__icons-container img {
  height: 1em;
}

.contact__container button {
  color: #fff;
  background-image: linear-gradient(to right, #409dea, #7fe3f9);
  border: none;
  border-radius: 50px;
  padding: 1em 2.4em;
  font-weight: 700;
  transition: transform .2s;
}

.contact__container button:hover {
  cursor: pointer;
  transform: scale(1.1);
}

footer {
  background-color: #0a0e1e;
  justify-content: center;
  align-items: center;
  padding: 3em 0;
  display: flex;
}

footer p {
  color: #aeb7be;
  font-size: .8em;
}

@media (max-width: 1500px) {
  .main__body {
    padding: 42vh 17em;
  }

  .project {
    padding: 5em 10em;
  }

  .projects-section h1 {
    margin: 0 3.3em;
    font-size: 3em;
  }

  .skills__container {
    padding: 7.5em 10em;
  }
}

@media (max-width: 1200px) {
  .main__navbar {
    padding: 5em;
  }

  .main__social-icons {
    left: 7em;
  }

  .main__body {
    padding: 42vh 12em;
  }

  .project {
    padding: 5em 7.5em;
  }

  .projects-section h1 {
    margin: 0 2.5em;
    font-size: 3em;
  }

  .skills__container {
    padding: 7.5em;
  }
}

@media (max-width: 1000px) {
  .project {
    flex-direction: column;
    display: flex;
  }

  .project-description, .project-thumbnail {
    width: 100%;
  }

  .skills__design, .skills__development {
    flex-direction: column;
    gap: 2em;
    display: flex;
  }

  .skills__design-right, .skills__design-left, .skills__development-right, .skills__development-left {
    width: 100%;
  }

  .skills__design-right, .skills__development-right {
    justify-content: center;
    display: flex;
  }
}

@media (max-width: 860px) {
  .main__container {
    height: 100vh;
    background-size: cover;
  }

  .main__navbar {
    padding: 5em;
  }

  .main__navbar ul {
    width: 100%;
    flex-direction: column;
    display: none;
  }

  .main__navbar ul.visible {
    height: 100vh;
    width: 100vw;
    background-image: url("Section-Background.5567bf85.png");
    justify-content: center;
    align-items: center;
    transition: all .2s;
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
  }

  .main__hamburger {
    display: block;
  }

  .main__hamburger .bar {
    transition: transform .5s, opacity .5s;
  }

  .main__hamburger.visible .bar:nth-child(2) {
    opacity: 0;
  }

  .main__hamburger.visible .bar:nth-child(1) {
    transform: translateY(8px)rotate(45deg);
  }

  .main__hamburger.visible .bar:nth-child(3) {
    transform: translateY(-8px)rotate(315deg);
  }

  .main__body {
    text-align: center;
    flex-direction: column;
    align-items: center;
    padding: 42vh 10em;
    display: flex;
  }

  .main__social-icons {
    left: 7em;
  }

  .project {
    padding: 5em;
  }

  .projects-section h1 {
    margin: 0 1.66em;
    font-size: 3em;
  }

  .skills__container {
    padding: 7.5em 5em;
  }
}

@media (max-width: 600px) {
  .main__navbar {
    padding: 5em 3em;
  }

  .main__body-header {
    font-size: 3em;
  }

  .main__body-subtitle {
    font-size: .8em;
  }

  .main__social-icons {
    left: 5em;
  }
}

